.glow {
    box-shadow: 0 0 5px white;
    animation: animate 1.5s linear infinite;
}

@keyframes animate {
    0% {
        box-shadow: 4px 4px 5px #528CCB, -4px -4px 5px #FFF457; 
    }
    50% {
        box-shadow: 4px 4px 5px #528CCB, -4px -4px 5px #FFF457,  -3px 3px .7em #F31D2F, 3px -3px .7em #F31D2F;
    }
    /* -3px 3px .7em #F31D2F, 3px -3px .7em #F31D2F; */
    /*  50% {
        box-shadow: 4px 4px 5px #528CCB, -4px -4px 5px #FFF457, -3px 3px .7em #F31D2F, 3px -3px .7em #F31D2F;
    }
    75% {
        box-shadow: 4px 4px 5px #528CCB, -4px -4px 5px #FFF457;
    } */
    100% {
        box-shadow: 4px 4px 5px #528CCB, -4px -4px 5px #FFF457;
    }
}

.glow:after {
    filter: blur(40px);
}
